<script>

// Component
import feather from 'feather-icons';
import Loader from '@/interface/template/Loader.vue';
import Subhead from '@/interface/template/outline/Subhead.vue';
import storageManager from '@/plugins/storage';

export default {
    inject: ['apiServer'],
    data() {
      return {
        hostkey:this.$Key,
        accessToken: storageManager.get('session','token'),
        configs: storageManager.get('configs'),
        loading_sources: true,
        isEditMode: false, // Track whether in edit mode
        showModal: false, // To control the modal visibility
        clientData: {
          clientId: '',
          status: true,
          source: 'mongodb',
          clientToken: '',
          connection: {
            URI: '',
            database: '',
            host: '',
            port: '',
            user: '',
            password: '',
          },
        },
        // Add a variable to store the item to edit
        listItemToEdit: null,
        listItem:[],
        showDeleteConfirmation: false,
        spaceToDelete: null,
      }
    },
    components: {
      Loader,
      Subhead
    },
    methods: {
      formatBytes(bytes) {
        var marker = 1024; // Change to 1000 if required
        var decimal = 3; // Change as required
        var kiloBytes = marker; // One Kilobyte is 1024 bytes
        var megaBytes = marker * marker; // One MB is 1024 KB
        var gigaBytes = marker * marker * marker; // One GB is 1024 MB
        if(bytes < kiloBytes) return bytes + " Bytes";
        else if(bytes < megaBytes) return(bytes / kiloBytes).toFixed(decimal) + " KB";
        else if(bytes < gigaBytes) return(bytes / megaBytes).toFixed(decimal) + " MB";
        else return(bytes / gigaBytes).toFixed(decimal) + " GB";
      },
      calculateDiskUsagePercentage(bytes,full) {
        const limitInBytes = full * 1024 * 1024; // 5000MB in bytes
        const percentage = (bytes / limitInBytes) * 100;
        return percentage.toFixed(2); // Display percentage with two decimal places
      },
      editItem(mode, item) {
        this.listItemToEdit = item; // Store the item to edit
        this.openModal(mode, item); // Open the modal with the mode and item's data
      },
      // Modify the openModal method to accept a mode parameter
      openModal(mode, initialFormData = {}) {
        this.isEditMode = mode === 'edit';
        if (mode === 'add') {
          // Initialize form data for adding a new client
          this.clientData = {
            clientId: '',
            status: true,
            source: 'mongodb',
            clientToken: '',
            connection: {
              URI: '',
              database: '',
              host: '',
              port: '',
              user: '',
              password: '',
            },
          };
        } else {
          // Initialize form data for editing an existing client
          this.clientData = { ...initialFormData }; // Replace with actual data for editing
        }
        this.showModal = true;
      },

      closeModal() {
        this.showModal = false;
      },
      submitForm() {
        if (this.isEditMode) {
          this.editData(this.listItemToEdit._id);
        } else {
          this.addData();
        }
      },
      async editData(id) {
        try {
          // Create a copy of this.clientData without the _id field
          const dataWithoutId = { ...this.clientData };
          delete dataWithoutId._id;

          const response = await fetch(`https://gateway.cloudrestfulapi.com/api/clients/${id}`, {
            method: 'PUT',
            headers: {
              'Content-Type': 'application/json','client-token-key':this.hostkey
            },
            body: JSON.stringify({ data: dataWithoutId }), // Send dataWithoutId without _id
          });

          if (response.ok) {
            console.log('Item updated successfully');
            await this.getData();
            this.closeModal();
          } else {
            console.error('Failed to update item');
          }
        } catch (error) {
          console.error(error);
        }
      },
      async addData() {
        try {
          const response = await fetch('https://gateway.cloudrestfulapi.com/api/clients', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json','client-token-key':this.hostkey
            },
            body: JSON.stringify({ data: this.clientData }), // Remove the extra curly braces
          });

          if (response.ok) {
            console.log('Item added successfully');
            await this.getData();
            this.closeModal();
          } else {
            console.error('Failed to add item');
          }
        } catch (error) {
          console.error(error);
        }
      },
      async getData() {
        if (storageManager.get('session', 'login')) {
          try {
            this.loading_sources = false;

            let andConditions = [{}];
            const pipeline = [
              {
                $match: {
                  $and: andConditions,
                },
              },
            ];

            const requestOptions = {
              method: 'POST',
              headers: { 'Content-Type': 'application/json','client-token-key':this.hostkey },
              body: JSON.stringify({
                pipeline: pipeline,
              }),
            };

            const resAPI = await fetch('https://gateway.cloudrestfulapi.com/api/clients/aggregate', requestOptions);
            const restReturn = await resAPI.json();

            this.listItem = restReturn;
            this.loading_sources = true;
          } catch (error) {
            console.log(error);
          }
        }
      },
      async deleteData(id) {
        if(storageManager.get('session','login')) {
          try {
            this.loading_sources = false;
            console.log("id",id)
            const resAPI = await fetch("https://gateway.cloudrestfulapi.com/api/space/" + id, {
              method: 'DELETE',
              headers: {'Content-Type': 'application/json','client-token-key':this.hostkey},
            });
            console.log(await resAPI);
            this.loading_sources = true;
            await this.getData();

          } catch (error) {
            console.log(error)
          }
        }
      },
      showDeleteDialog(space) {
        this.spaceToDelete = space;
        this.showDeleteConfirmation = true;
      },
      async confirmDelete() {
        try {
          await this.deleteData(this.spaceToDelete._id);
          this.showDeleteConfirmation = false;
        } catch (error) {
          console.error(error);
        }
      },
      cancelDelete() {
        this.showDeleteConfirmation = false;
      },
      updated() {
        feather.replace();
      },
      revokeToken() {
        // Generate a new random key (you can use any method to generate a random key)
        const newToken = this.generateRandomToken();
        // Assign the new token to clientData.clientToken
        this.clientData.clientToken = newToken;
        // Optionally, you can also save the new token to your server or perform any other actions
        console.log('Token revoked and replaced with a new random key');
      },
      generateRandomToken() {
        // Generate a random alphanumeric token (you can customize this)
        const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        const tokenLength = 32; // Adjust the length of the token as needed
        let randomToken = '';
        for (let i = 0; i < tokenLength; i++) {
          const randomIndex = Math.floor(Math.random() * characters.length);
          randomToken += characters.charAt(randomIndex);
        }
        return randomToken;
      },
    },
    async mounted () {
      try {
        await this.getData();
      } catch (error) {
        console.log(Error);
      }
    },
    setup() {
      //console.log("Setup");
    },
};
</script>

<template>

  <div v-if="!loading_sources" class="text-center"><Loader/></div>
  <Subhead  v-if="loading_sources"
    :navigation="
    [
      {
        name: 'ย้อนกลับ',
        link: '/',
        style: 'chevron-left',
        class: 'default-btn',
        visible: true,
        type: 'button',
      },
    ]"
  />
  <!-- Modal -->
  <div
    class="fixed top-0 left-0 w-full h-full flex items-center justify-center z-50"
    v-if="showModal"
  >
    <div class="fixed inset-0 bg-black opacity-50"></div>
    <div class="relative bg-white w-full md:w-1/2 lg:w-1/2 rounded shadow-lg">
      <!-- Modal Header -->
      <div class="bg-blue-500 text-white p-3 rounded-t">
        <h3 class="text-md font-semibold"><font-awesome-icon :icon="['fas','server']" class="text-white mr-1" /> {{ isEditMode ? 'แก้ไข Space' : 'เพิ่ม Space ใหม่' }}</h3>
        <button class="absolute top-2 right-3 text-2xl font-semibold hover:text-gray-200"
        @click="closeModal">&times;</button>
      </div>

      <!-- Modal Content -->
      <div class="p-3">
        <div class="grid grid-cols-2 gap-2">
          <div class="mb-2">
            <label for="name" class="block text-gray-700 font-bold mb-2">Client Name</label>
            <input
              v-model="clientData.clientId"
              type="text"
              id="clientId"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>
          <div class="mb-2">
            <label for="source" class="block text-gray-700 font-bold mb-2">Client Source</label>
            <select v-model="clientData.source" id="source" class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500">
              <option value="mongodb">MongoDB</option>
              <option value="postgresql">PostgreSQL</option>
              <option value="firestore">Filebase</option>
              <option value="api">Rest API</option>
              <option value="mysql">MySQL</option>
            </select>
          </div>

          <div class="col-span-2 mb-2">
            <label for="clientToken" class="block text-gray-700 font-bold mb-2">Client Token</label>
            <input
              v-model="clientData.clientToken"
              type="text"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
            <div v-if="clientData.clientToken">
              <!-- Display a button to revoke token if clientData.clientToken has data -->
              <button
                @click="revokeToken"
                class="mt-2 px-4 py-2 text-white bg-red-500 rounded hover:bg-red-600"
              >
                Revoke Token
              </button>
            </div>
            <div v-else>
              <!-- Display a button to add a new token if clientData.clientToken is empty -->
              <button
                @click="revokeToken"
                class="mt-2 px-4 py-2 text-white bg-green-500 rounded hover:bg-green-600"
              >
                Add Token
              </button>
            </div>
          </div>
          

        </div>

        <div class="grid grid-cols-2 gap-2" v-if="clientData.source === 'mongodb'">
          
          <div class="col-span-2 mb-2">
            <label for="URI" class="block text-gray-700 font-bold mb-2">MongoDB URI</label>
            <input
              v-model="clientData.connection.URI"
              type="text"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>
          <div class="mb-2">
            <label for="s3Hosting" class="block text-gray-700 font-bold mb-2">MongoDB Database</label>
            <input
              v-model="clientData.connection.database"
              type="text"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>
        </div>

        <div class="grid grid-cols-2 gap-2" v-if="clientData.source === 'mysql'">
          <div class="mb-2">
            <label for="s3Secret" class="block text-gray-700 font-bold mb-2">MySQL Host</label>
            <input
              v-model="clientData.connection.host"
              type="text"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>

          <div class="mb-2">
            <label for="s3Bucket" class="block text-gray-700 font-bold mb-2">MySQL port</label>
            <input
              v-model="clientData.connection.port"
              type="text"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>

          <div class="mb-2">
            <label for="s3Region" class="block text-gray-700 font-bold mb-2">MySQL user</label>
            <input
              v-model="clientData.connection.user"
              type="text"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>

          <div class="mb-2">
            <label for="s3Bucket" class="block text-gray-700 font-bold mb-2">MySQL password</label>
            <input
              v-model="clientData.connection.password"
              type="text"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>

          <div class="mb-2">
            <label for="s3EndpointDefault" class="block text-gray-700 font-bold mb-2">MySQL database</label>
            <input
              v-model="clientData.connection.database"
              type="text"
              class="w-full border border-gray-300 p-2 rounded focus:outline-none focus:border-blue-500"
            />
          </div>
        </div>

        <div class="grid grid-cols-2 gap-2">
          <div class="mb-2">
            <label class="block text-gray-700 font-bold mb-2">สถานะ</label>
            <input
              type="checkbox"
              v-model="clientData.status"
              class="form-checkbox h-5 w-5 text-blue-600"
            /> ใช้งาน
          </div>
        </div>
      </div>

      <!-- Modal Footer -->
      <div class="bg-gray-100 px-4 py-3 flex justify-end rounded-b">
        <button
          type="button"
          class="px-4 py-2 text-white bg-blue-500 rounded hover:bg-blue-600 mr-2"
          @click="closeModal"
        >
          ปิด
        </button>
        <button
          type="button"
          @click="submitForm"
          class="px-4 py-2 text-white bg-green-500 rounded hover:bg-green-600"
        >
          <font-awesome-icon :icon="['fas','save']" class="text-white mr-1" />
          {{ isEditMode ? 'อัพเดตข้อมูล' : 'เพิ่มข้อมูล' }}
        </button>
      </div>
    </div>
  </div>

  <div class="flex-1 pb-8 bg-gray-100 pt-2 pb-5 border-t" v-if="loading_sources">
    <div class="mt-8">
      <div class="mx-auto max-w-7xl px-6 sm:px-6 lg:px-6">

        <div class="mt-8 flex flex-col">
          <div class="-my-2 -mx-4 overflow-x-auto sm:-mx-6 lg:-mx-8">
            <div class="inline-block min-w-full py-2 align-middle md:px-6 lg:px-8">

              <div class="grid grid-cols-2 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4">
             
                <div v-for="(client) in listItem" :key="client._id" class="bg-white rounded-lg shadow-md overflow-hidden">
                  <div class="w-full h-30 relative text-center object-cover pt-10">

                    <div class="w-7 h-7 absolute top-2 right-2 text-white pt-[6px] text-sm font-normal rounded-full text-center" :class="client.status ? 'bg-green-600' : 'bg-red-600'">
                      <font-awesome-icon :icon="client.status ? ['fas', 'check-circle'] : ['fas', 'times']" class="text-white" />
                    </div>
                    
                    <div class="flex flex-col items-center justify-center">

                      <div class="relative text-center w-1/2" v-if="client.source === 'mysql'">
                        <img src="https://vue-project.sgp1.digitaloceanspaces.com/2023/12/1702563466307.jpg" height="100" alt="Image Alt Text">
                      </div>

                      <div class="relative text-center w-1/2" v-if="client.source === 'mongodb'">
                        <img src="https://vue-project.sgp1.digitaloceanspaces.com/2023/12/1702563470917.jpg" height="100" alt="Image Alt Text">
                      </div>
                      
                      <div class="relative text-center w-1/2" v-if="client.source === 'postgresql'">
                        <img src="https://vue-project.sgp1.digitaloceanspaces.com/2023/12/1702563469914.jpg" height="100" alt="Image Alt Text">
                      </div> 
                      
                      <div class="relative text-center w-1/2" v-if="client.source === 'firestore'">
                        <img src="https://vue-project.sgp1.digitaloceanspaces.com/2023/12/1702563468668.jpg" height="100" alt="Image Alt Text">
                      </div>

                      <div class="relative text-center w-1/2" v-if="client.source === 'api'">
                        <img src="https://vue-project.sgp1.digitaloceanspaces.com/2023/12/1702563467701.jpg" height="100" alt="Image Alt Text">
                      </div> 

                    </div>
                  </div>               
                  <div class="p-4">
                    
                    <p class="font-bold text-gray-600">{{ client.clientId }}</p>
                    <p class="text-xs text-gray-400">{{client.clientToken}}</p>
                    <p class="text-xs text-gray-400">{{client.source}}</p>
                    <p class="text-xs text-gray-400" v-if="client.analytics && client.analytics.read !== null && client.analytics.read !== undefined && client.analytics.write !== null && client.analytics.write !== undefined">
                      Bandwidth (R:{{ client.analytics.read }} W:{{ client.analytics.write }})
                    </p>
                    <p class="text-xs text-gray-400" v-else>
                      Bandwidth (R:0 W:0)
                    </p>
                  </div>
                  <div class="bg-gray-100 border-t-2 border-gray-200 py-2 px-4 flex justify-between items-center">
                    <a href="javascript:void(0);" @click="editItem('edit',client)" class="text-gray-900 hover:text-gray-500 text-md font-medium">
                      <font-awesome-icon :icon="['fas','square-pen']" class="text-black mt-[3px] mr-2 text-md"/>
                      <span>แก้ไข</span>
                    </a>
                    <a href="javascript:void(0);" @click="showDeleteDialog(client)" class="text-gray-900 hover:text-gray-500 text-md font-medium">
                      <font-awesome-icon :icon="['fas','trash-can']" class="text-black mt-[3px] mr-2 text-md"/>
                      <span>ลบ</span>
                    </a>
                  </div>
                </div>

                <!-- Button to open the modal for adding -->
                <button class="bg-white p-10 rounded-lg shadow-md overflow-hidden flex flex-col items-center justify-center cursor-pointer p-10"
                @click="openModal('add')"
                >
                  <div class="w-12 h-12 rounded-full bg-blue-500 text-white flex items-center justify-center hover:opacity-70">
                    <font-awesome-icon :icon="['fas','plus']" class="text-3xl"/>
                  </div>
                  <p class="text-gray-500 mt-2 text-center">Add Client</p>
                </button>
                
              </div>
              
            </div>
          </div>
        </div>


    </div>
  </div>
</div>

<!-- Delete confirmation dialog (inline) -->
<div v-if="showDeleteConfirmation" class="fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50">
  <!-- Modal -->
  <div class="bg-white rounded shadow-lg w-96">
    <!-- Modal Header -->
    <div class="bg-red-500 text-white p-2 rounded-t">
      <h3 class="text-base"><font-awesome-icon :icon="['fas','trash']" class="text-white"/> ยืนยันการลบข้อมูล</h3>
    </div>
    <!-- Modal Content -->
    <div class="p-4">
      <p class="text-red-500 text-center">
        คุณต้องการลบข้อมูล <strong>"{{ spaceToDelete.name }}"</strong> ? <br> หลังจากลบแล้ว ไม่สามารถกู้คืนได้
      </p>
    </div>
    <!-- Modal Footer -->
    <div class="bg-gray-100 py-2 px-4 flex justify-end rounded-b">
      <button
        @click="cancelDelete"
        class="px-2 py-1 text-gray-500 hover:text-gray-700 mr-2"
      >
        ยกเลิก
      </button>
      <button
        @click="confirmDelete"
        class="px-2 py-1 text-red-500 hover:text-red-700 font-medium"
      >
        ลบเลย
      </button>
    </div>
  </div>
</div>

</template>